/* ------------------------------------------------- Responsividade */
@media only screen and (max-width: 1600px){

}

@media only screen and (max-width: 1440px){
  .fundo-img {
    width: 100%;
    margin-top: 6px;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    justify-content: center;
    background-size: cover;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.555);
  }
  .h1-header {
    text-align: center;
    font-family: "Raleway", sans-serif;
    text-transform: inherit;
    padding: 30px 0 0;
    color: rgb(0, 0, 0);
    animation: text 2s 1;
  }
  @keyframes text {
    0% {
      color: white;
      margin-bottom: -10px;
    }
    30% {
      letter-spacing: 10px;
      margin-bottom: -10px;
    }
  }
  .paragrafo ul li {
    list-style: none;
    text-align: center;
    font-style: italic;
    color: rgb(0, 0, 0);
    font-family: "Open Sans", sans-serif;
    animation: text1 2s 1;
  }
  @keyframes text1 {
    0% {
      color: white;
      margin-bottom: -40px;
    }
  }
  .alinhamento {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  
  .alinhadiv1 {
    display: flex;
    justify-content: center;
  }
  
  .titulo {
    text-align: center;
    font-family: "Raleway";
    text-transform: capitalize;
    font-size: 28px;
    color: rgba(3, 3, 3, 0.712);
  }
  
  .alinhadiv2 {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .botao {
    background-color: Transparent;
    border-style: double;
    padding: 3px 10px;
    border-radius: 15px;
    font-family: "Raleway";
    font-size: 16px;
    border: 1px solid rgba(0, 0, 0, 0.795);
    box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.308);
  }
  .botao:hover {
    color: #f88f06;
    border-color: #f88f06;
  }
  #form {
    padding: 0 100px 0 10px;
    margin-right: 50px;
    border-radius: 10px;
    border: 1px solid rgba(0, 0, 0, 0.795);
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.425);
  }
  
  .form-control {
    margin-bottom: 20px;
    color: #fff;
  }
  
  .form-label {
    margin-bottom: 0.5rem;
  }
  /* --------------------------------------------------- Parte debaixo Corpo */
  .contato-header nav ul li a {
      text-decoration: none;
    }
    .corpo-contato {
      display: flex;
      padding: 0 80px;
      color: #fff;
      align-items: center;
      background-color: #fff;
      border-top: 1px solid rgba(34, 34, 34, 0.404);
      box-shadow: 15px 15px 50px rgb(0 0 0);
    }
    
    .dados-corpo{
      margin-bottom: 35px;
    }
  
  .dados-corpo a {
      color: rgba(0, 0, 0, 0.644);
      text-decoration: none;
    }
    .dados-corpo a:hover {
      text-decoration: none;
      color: #f88f06;
    }
    .horarios-corpo {
      color: rgba(0, 0, 0, 0.644);;
      margin-bottom: 35px;
    }
    .horarios-corpo a {
      color: rgba(0, 0, 0, 0.644);
      text-decoration: none;
    }
    .horarios-corpo a:hover{
      text-decoration: none;
      color: #f88f06;
    }
    .endereco-corpo{
      margin-bottom: 35px;
    }
    .endereco-corpo a {
      color: rgba(0, 0, 0, 0.644);
      text-decoration: none;
    }
    .endereco-corpo a:hover {
      text-decoration: underline;
      color: #f88f06;
    }
    .h2-corpo-contato {
      color: rgba(3, 3, 3, 0.712);
      margin-top: 35px;
    }
    .menu-footer-contato {
      color: #4d4d4d;
    }
    .menu-footer-contato:hover {
      color: white;
    }
    .sobre-footer-contato {
      color: #4d4d4d;
    }
    .sobre-footer-contato:hover {
      color: white;
    }
    .contato-footer-contato {
      color: #f15939;
    }
    .contato-footer-contato:hover {
      color: white;
    }

}

